

.main-container {
    background-image: url(../../assets/images/rain.jpeg);
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    opacity: 0.8;
    display: flex;
    flex-direction: column;
}

.brand {
    display: block;
    position: absolute;
    z-index: 1;
    margin: 0 auto;
    padding: 3rem;
    font-size: 2rem;
    font-weight: 500;
    font-family: 'Pacifico', cursive;
}

.main {
    position: fixed;
    bottom: 10rem;
    width: 50vh;
    margin: 0 auto;
    transform: translate(50%);
    display: flex;
    gap: 2rem;
    align-items: baseline;
    color: #fff;
    font-size: 2rem;
    font-weight: 300;
}

.main .cityname {
    font-size: 3rem;
    font-weight: 600;
}

.info {
    background-color: #50505096;
    position: fixed;
    right: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    backdrop-filter: blur(10px);
    color: #eee;
    padding: 7rem 3rem;
    height: 100vh;
    width: 25%;
}

input {
    background-color: transparent;
    border: 1px solid #eee;
    outline: none;
    color: #fff;
    padding: 1rem;
}

button {
    background-color: transparent;
    border: 1px solid #eee;
    color: #fff;
    padding: 1rem;
    margin-left: 2px;
}


  /* For smaller screens */
  @media (max-width: 768px) {
    .main-container {
      /* background-image: none; Remove background image on smaller screens */
      background-color: #f2f2f2; /* Set a background color */
      padding: 0px; /*Add some padding to the container */
    }
  
    .main {
      /* Adjust styling for smaller screens */
      position: relative;
      bottom: auto;
      width: 100%;
      margin: 0;
      transform: none;
      display: flex;
      flex-direction: column; /* Stack text vertically */
      gap: 0rem;
      align-items: center;
      text-align: center;
      font-size: 1.5rem;
    }

    .main .cityname {
    margin-top: -1rem;
    }
  
    .info {
      /* Adjust styling for smaller screens */
      position: static;
      padding: 2rem;
      height: auto;
      width: 100%;
    }
  }
  
  .weather-details {
    border-collapse: collapse;
    width: 100%;
    margin-top: 1rem;
  }
  
  .weather-details caption {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .weather-details td {
    padding: 0.5rem;
  }
  
